<template>
  <b-container fluid>
    <!-- <pre>{{relatedProducts}}</pre> -->
    <!-- <pre>{{images}}</pre> -->
    <b-row>
      <b-col v-for="(brand, idx) in brands" :key="idx" lg="2" md="3" sm="4" cols="6" class="my-2">
        <brand-box :brand="brand"></brand-box>
      </b-col>
    </b-row>
    <b-container fluid class="d-flex justify-content-center">
      <b-overlay :show="busy" opacity="0.4" spinner-small
                        spinner-variant="primary" class="d-inline block">
        <b-button variant="success" :disabled="busy" v-if="!productLoadingDone" @click="loadMore">
          Load More
        </b-button>
      </b-overlay>
    </b-container>
  </b-container>
</template>

<script>
import loader from '../../mixins/loader'
import BrandBox from '../../components/Boxes/BrandBox'

export default {
  name: 'PopularBrands',
  data () {
    return {
      brands: [],
      productLoadingDone: true,
      busy: false
    }
  },
  computed: {
    relatedProducts () {
      const limit = 10
      return this.brands.filter(brand => brand.products && brand.products.length).map(brand => brand.products[0]).slice(0, limit)
    },
    images () {
      const limit = 6
      return this.brands.filter(brand => brand.images && brand.images.length).map(brand => brand.images[0]).slice(0, limit)
    }
  },
  components: {
    BrandBox
  },
  mixins: [
    loader
  ],
  methods: {
    loadMore () {
      if (!this.productLoadingDone) this.busy = true
      const offset = this.brands.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/top-brands', { offset, limit }, 'top-brands')
        .then(response => {
          this.brands = this.brands.concat(response.data.brands)
          this.busy = false
          this.productLoadingDone = response.data.brands.length < limit
        })
    }
  },
  created () {
    this.loadMore()
  }
}
</script>

